import React from "react"
import { Link } from 'react-router-dom';

export default class Faq extends React.Component {
    render() {
        return (
            <div className='help-page center-page-x center'>
                <h1>Help Center</h1>
                <div>
                    <h2>Documentation</h2>
                    <div className='help-index'>
                        <div>
                            <Link to='/help/uploading'>Creating a new project</Link><br />
                        </div>
                        <div>
                            <Link to='/help/projectDetails'>Manage a project</Link><br />
                        </div>
                        <div>
                            <Link to='/help/editing'>Using Ezyvue Studio</Link><br />
                        </div>
                        <div>
                            <Link to='/help/viewer'>Using the Viewer</Link><br />
                        </div>
                     {/*    <div>
                            <Link to='/help/javascriptAPI'>Javascript Api</Link><br />
                        </div> */}
                    </div>
                    <h2>Guides</h2>
                    <h3>Integrating the viewer</h3>
                    <div className='help-index'>
                        <div>
                            <Link to='/help/embedViewer'>Integrate with your website</Link><br />
                        </div>
                        <div>
                            <Link to='/help/embedViewerOnWordPress'>Integrate with WordPress</Link><br />
                        </div>
                        <div>
                            <Link to='/help/embedViewerOnShopify'>Integrate with Shopify</Link><br />
                        </div>
                        <div>
                            <Link to='/help/embedViewerOnWix'>Integrate with Wix</Link><br />
                        </div>
                        <div>
                            <Link to='/help/embedViewerOnSquarespace'>Integrate with Squarespace</Link><br />
                        </div>
                        <div>
                            <Link to='/help/embedViewerOnWebflow'>Integrate with Webflow</Link><br />
                        </div>
                    </div>
                    <h2>Need Help?</h2>
                    <p>
                        We&apos;re here to help resolve any problems you have.<br/>
                        <Link to='/contact'>Get in touch</Link>
                    </p>
                </div>
            </div>
        );
    }
}
